.coin p{
  font-family: georgia;
  font-style: italic;
  position: absolute;
  font-size: 12px;
  z-index: 700;
  top: 3px;
  left: 0px;
  padding-left: 5px;
  }

.coin.bronze p{  color: rgba(223,182,103,1);}
.coin.silver p{  color: rgba(160,160,160,1);}
.coin.gold p{  color: rgba(242,215,50,1);}

.coin{
  cursor: pointer;
  content: "";
  width: 25px; 
  height: 25px;
  display: inline-block;
  position: relative;
  margin: 5px;
  top: 10px;
  border-radius: 50%;
  z-index: 500;
  box-shadow:  2px 2px 2px 1px rgba(0, 0, 0, .1);
  }

.coin:after{
  content: "";
  width: 19px; 
  height: 19px;
  display: block;
  top: 3px;
  left: 3px;
  position: absolute;
  border-radius: 50px;
  z-index: 600;
  }

.coin:before{
  content: "";
  width: 22px; 
  height: 22px;
  top: 1.5px;
  left: 1.5px;
  display: block;
  position: absolute;
  border-radius: 50px;
  z-index: 500;
  }

.coin:hover{
  transition: all .5s ease-in-out;

  }

